<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>
    <div class="content__catalog">
      <ProductFilter :priceFrom.sync="filterPriceFrom" :priceTo.sync="filterPriceTo" :categoryId.sync="filterCategoryId" :colorId.sync="filterColorId"/>
      <section class="catalog">
        <div v-if="productsLoading">Products loading... <img src="img/4.gif" alt="loading"></div>
        <div v-if="productsLoadingFailed">Loading error...<button @click.prevent="loadProducts">Again</button></div>
        <ProductList :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="produtsPerPage"/>
      </section>
    </div>
  </main>
</template>
<script>
  import ProductList from '@/components/ProductList.vue';
  import BasePagination from '@/components/BasePagination.vue';
  import ProductFilter from '@/components/ProductFilter.vue';
  import axios from 'axios';
  import {API_BASE_URL} from '@/config'

  export default {
    name: 'MainPage',
    components: {ProductList, BasePagination, ProductFilter},
    data() {
      return {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColorId: 0,

        page: 1,
        produtsPerPage: 3,

        productsData: null,

        productsLoading: false,
        productsLoadingFailed: false,
      }
    },
    computed: {
      products() {
        return this.productsData ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url
          }
        })
        : []
      },
      countProducts(){
        return this.productsData ? this.productsData.pagination.total : 0;
      }
    },
    watch: {
      page() {
        this.loadProducts();
      },
      filterPriceFrom() {
        this.loadProducts();
      },
      filterPriceTo() {
        this.loadProducts();
      },
      filterCategoryId() {
        this.loadProducts();
      },
      filterColorId() {
        this.loadProducts();
      },
    },
    methods: {
      loadProducts() {
        this.productsLoading = true;
        this.productsLoadingFailed = false;
        clearTimeout(this.loadProductsTimer);
        this.loadProductsTimer = setTimeout(() => {
          axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.produtsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId
            }
          })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false)
        }, 2000);
      },
    },
    created() {
      this.loadProducts();
    }
  };
</script>
